import React from 'react';
import images from "../api";
import Card from "./Card";  
import imagesrc from "./imagesrc.js";
import imagesrc2 from './imagesrc2';

const Gallery =() =>{
    
    const cards = images.map((image,i)=>{  
        return <Card key={i} 
        id={images[i].id} 
        heading={images[i].heading}
         detail={images[i].detail}
          src={imagesrc[i]}
          src2={imagesrc2[i]}/>
    })
    return(
        <div className="cards">
            {cards}  {/* cards component  that iterates over all the value from images api and pass the detaiils as props to card*/ }
        </div>
    );
}

export default Gallery;