import React from 'react';



const Card = ( {heading , detail , src ,src2, id} )=>{

    if(src2){
        return(
            <div className='card'>
                     <h1 className="card_heading">{heading}</h1>
                <p className ='card_detail'> hii this is a sample text</p>     
                <div className="card_box2">
                    <div className='part'><img src={src2} alt="phot0" className="card_image2"/></div>
                    <div className='part'>
                    <img src={src} alt="phot0" className="card_image2"/>
                    </div>
                    
                </div>
            </div>
    );
         }

    else{
         return(
                <div className='card'>
                <h1 className="card_heading">{heading}</h1>
                <p className ='card_detail'>hii this is a sample text</p> 
                <div className="card_box">
                <img src={src} alt="phot0" className="card_image"/>  
                </div>
            </div>
             );
         }

}

export default Card;